import {Http} from "../service";

/**
 * Constants
 */

export const GET_SINGLE_MLP_REQUEST = "GET_SINGLE_MLP_REQUEST";
export const GET_SINGLE_MLP_SUCCESS = "GET_SINGLE_MLP_SUCCESS";
export const GET_SINGLE_MLP_FAILURE = "GET_SINGLE_MLP_FAILURE";

export const initialState = {
   singleMLP: {},
   loadingOfMLP: false,
   errorOfMLP: null,
};

/**
 * Reducer
 */

export default (state = initialState, action) => {
   switch (action.type) {
      case GET_SINGLE_MLP_REQUEST:
         return {
            ...state,
            singleMLP: {},
            loadingOfMLP: true,
            errorOfMLP: null,
         };

      case GET_SINGLE_MLP_SUCCESS:
         return {
            ...state,
            singleMLP: action.payload,
            loadingOfMLP: false,
            errorOfMLP: null,
         };

      case GET_SINGLE_MLP_FAILURE:
         return {
            ...state,
            singleMLP: {},
            loadingOfMLP: false,
            errorOfMLP: action.error,
         };

      default:
         return state;
   }
};

/**
 * Actions
 */

export const getSingleMLP = mlpId => {
   const request = () => ({type: GET_SINGLE_MLP_REQUEST});
   const success = mlp => ({type: GET_SINGLE_MLP_SUCCESS, payload: mlp});
   const failure = error => ({type: GET_SINGLE_MLP_FAILURE, error});

   return dispatch => {
      dispatch(request());

      const formData = new FormData();

      formData.append('key', mlpId);

      Http.getSingleMLP(formData)
         .then(res => res.data.ok
            ? dispatch(success(res.data.mlp))
            : dispatch(failure(res.data))
         )
         .catch(err => dispatch(failure(err.messages)))
   };
};