import axios from 'axios';

const baseUrl = 'https://api.chatlead.io/app/api';

const instance = axios.create({
   baseURL: baseUrl,
   headers: {
      'Content-Type': 'application/json',
   }
});

export const Http = {
   getSingleMLP: mlpId => instance.post('/GetMlpGuest/', mlpId)
};