import React from 'react';

import {Result, Button} from "antd";

const ErrorPage = ({title, subTitle}) => (
   <Result
      style={{paddingTop: '5%'}}
      status="404"
      title={title}
      // subTitle="Извините, вы ввели несуществующий URL."
      subTitle={subTitle}
      extra={
         <a href="https://chatlead.io/" target="_blank" rel="noopener noreferrer">
            <Button type="primary">Chatlead.io</Button>
         </a>
      }
   />
);

export default ErrorPage;